body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.mapContainer {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}

.mapContainer .mapboxgl-canvas-container {
  width: 100%;
  height: 100%;
  position: relative;
}

.marker {
  background-color: #919ad7;
  opacity: 0.7;
  border-radius: 50%;
  cursor: pointer;
}
